<template>
  <div class="pulsevideo" id="pulsevideo">
    <div class="cover"></div>
    
      <div :class="`overlay ${hideTransition ? `hideTransition` : ``}`">
        <transition appear name="fadein">
          <h1>P U L S E</h1>
        </transition>
      </div>

      <youtube 
        v-if="link"
        id="video" 
        :video-id="link " 
        :player-vars="{ autoplay: hideTransition ? 1 : 0, rel: 0, modestbranding: 1 }" 
        :key="hideTransition"
        @ended="ended"
        >
      </youtube>
   <!-- </transition> -->
  </div>
</template>

<script>
import prismic from "../prismic.js";

export default {
  name: 'PulseVideo',
  components: {
  },
  data() {
    return {
      
      hideTransition: false,
      link: null
    }
  },
  props: {
    mousemoveObj: {
      type: Object,
      default: () => {}
    },
    clickObj: {
      type: Object,
      default: () => {}
    },
  },
  watch: {
  },
  methods: {
    ended() {
      window.location.href = "./index.html#/welcome";
    },
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.hideTransition = true;
      }, 1000);
    })
  },
  beforeMount() {
    // in case someone tries accessing via link and they are offline
    if (!window.navigator.onLine) window.location.href = "./index.html";
      prismic.getData().then((assets) => {
      this.link = this.$cms.textField(assets.pulse_video_youtube_id);
      })

    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.pulsevideo {
  height: 100vh;
  h1 {
    top: 50%;
    position: fixed;
    transform: translateY(-50%);
    width: 100%;
    left: 0;
  }
}
#video {
  ::v-deep iframe {
    width: 100vw;
    height: 100vh;
  }
  ::v-deep .ytp-pause-overlay {
    display: none !important;
  }
}

</style>
